/* NavbarCamera Camera */

.NavbarCamera {
  display: flex;

  background-color: #333;
  position: fixed;
  bottom: 0;
  width: 100%;
}

.NavbarCamera a {
  float: left;
  display: block;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
}

.NavbarCamera a:hover {
  background: #f1f1f1;
  color: black;
}

.NavbarCamera a.active {
  background-color: #04aa6d;
  color: white;
}

.main {
